'use client';
import { useRouter } from 'next/navigation';
import { useState } from 'react';

import styles from './Form.module.css';

import Alerta from '@/components/common/feedback/Alerta';
import { useAuth } from '@/hooks/useAuth';

export default function RegisterForm() {
  const { registerUser, error } = useAuth();
  const router = useRouter();

  const [form, setForm] = useState({
    email: '',
    password: '',
    nombres: '',
    apellidos: '',
    username: '',
    direccion: {
      pais: '',
      calle: '',
      numero: '',
      colonia: '',
      codigoPostal: '',
      ciudad: '',
      estado: '',
    },
  });

  const [mostrarAlerta, setMostrarAlerta] = useState(false);

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await registerUser(form);
      router.push('/auth/login');
    } catch (err) {
      console.error('Error al registrar', err);
      setMostrarAlerta(true);
    }
  };

  return (
    <>
      {mostrarAlerta && error && (
        <Alerta tipo="error" mensaje={error} onClose={() => setMostrarAlerta(false)} />
      )}

      <form onSubmit={handleRegister} className={styles.form}>
        <input
          className={styles.input}
          type="text"
          value={form.nombres}
          onChange={(e) => setForm({ ...form, nombres: e.target.value })}
          placeholder="*Nombres"
          required
        />
        <input
          className={styles.input}
          type="text"
          value={form.apellidos}
          onChange={(e) => setForm({ ...form, apellidos: e.target.value })}
          placeholder="*Apellidos"
          required
        />
        <input
          className={styles.input}
          type="text"
          value={form.username}
          onChange={(e) => setForm({ ...form, username: e.target.value })}
          placeholder="Username"
        />
        <input
          className={styles.input}
          type="email"
          value={form.email}
          onChange={(e) => setForm({ ...form, email: e.target.value })}
          placeholder="*Correo"
          required
        />
        <input
          className={styles.input}
          type="password"
          value={form.password}
          onChange={(e) => setForm({ ...form, password: e.target.value })}
          placeholder="*Contraseña"
          required
        />

        {/* 🏠 Sección de dirección */}
        <section className={styles.direccion}>
          <h3 className={styles.subtitulo}>Dirección</h3>
          <input
            className={styles.input}
            type="text"
            value={form.direccion.pais}
            onChange={(e) =>
              setForm({
                ...form,
                direccion: { ...form.direccion, pais: e.target.value },
              })
            }
            placeholder="*País"
            required
          />
          <input
            className={styles.input}
            type="text"
            value={form.direccion.estado}
            onChange={(e) =>
              setForm({
                ...form,
                direccion: { ...form.direccion, estado: e.target.value },
              })
            }
            placeholder="Estado"
          />
          <input
            className={styles.input}
            type="text"
            value={form.direccion.ciudad}
            onChange={(e) =>
              setForm({
                ...form,
                direccion: { ...form.direccion, ciudad: e.target.value },
              })
            }
            placeholder="Ciudad"
          />
          <input
            className={styles.input}
            type="text"
            value={form.direccion.colonia}
            onChange={(e) =>
              setForm({
                ...form,
                direccion: { ...form.direccion, colonia: e.target.value },
              })
            }
            placeholder="Colonia"
          />
          <input
            className={styles.input}
            type="text"
            value={form.direccion.calle}
            onChange={(e) =>
              setForm({
                ...form,
                direccion: { ...form.direccion, calle: e.target.value },
              })
            }
            placeholder="Calle"
          />
          <input
            className={styles.input}
            type="text"
            value={form.direccion.numero}
            onChange={(e) =>
              setForm({
                ...form,
                direccion: { ...form.direccion, numero: e.target.value },
              })
            }
            placeholder="Número"
          />
          <input
            className={styles.input}
            type="text"
            value={form.direccion.codigoPostal}
            onChange={(e) =>
              setForm({
                ...form,
                direccion: { ...form.direccion, codigoPostal: e.target.value },
              })
            }
            placeholder="Código Postal"
          />
        </section>

        <button className={styles.boton} type="submit">
          Registrarme
        </button>
        <a href="/auth/login" className={styles.link}>
          ¿Ya tienes cuenta? Inicia sesión
        </a>
      </form>
    </>
  );
}
